<template>
  <div>
    <HeaderTittle Title=""></HeaderTittle>
    <div style="margin:10px">
      <div class="left-content">
        ()
        <input v-model="r">
        ()
        <input v-model="seeHeight">
        (0)
        <input v-model="testCount" value="0">
        <button @click="startTest" style="margin-left:10px"></button>
      </div>
      <div class="left-content">0.001,0.4999</div>
      <br>
      <HotTable
        :root="root"
        ref="testHot"
        :settings="hotSettings"
        style="overflow:auto; min-height: 400px;"
      ></HotTable>
      <div class="left-content">{{info}}</div>
    </div>
    <div v-bind:class="{coverLayout:isOnTesting}">
      <div style="font-size:50px;margin-top:400px;">...</div>
    </div>
  </div>
</template>

<script>
import { HotTable } from "@handsontable/vue";
import "../../node_modules/handsontable/dist/handsontable.full.css";
import Handsontable from "handsontable";
import { truncate } from "fs";
import HeaderTittle from "./HeaderTittle.vue";

export default {
  name: "handsonTable",
  data: function() {
    return {
      info: "",
      r: 0.01,
      seeHeight: 2,
      testCount: 10,
      isOnTesting: false,
      root: "test-hot",
      hotSettings: {
        data: [],
        minRows: 1, //
        minCols: 5,
        maxRows: 10000, //
        maxCols: 20,
        rowHeaders: true, //html

        // allPointCount: 5184
        // fosdErrRate: 0
        // fosdPerPointTime: 52.29552469135802
        // fosdTime: 0.0002711
        // fosdUnVisibleErrCount: 0
        // fosdUnVisibleErrRate: 0
        // fosdVisibleErrCount: 0
        // fosdVisibleErrRate: 0
        // osdErrRate: 0
        // osdPerPointTime: 123.82330246913578
        // osdTime: 0.0006418999999999999
        // osdUnVisibleErrCount: 0
        // osdUnVisibleErrRate: 0
        // osdVisibleErrCount: 0
        // osdVisibleErrRate: 0
        // r: 72
        // r3Time: 0.0076614
        // refFErrRate: 0.0015432098765432098
        // refFPerPointTime: 31.558641975308642
        // refFTime: 0.0001636
        // refFUnVisibleErrCount: 0
        // refFUnVisibleErrRate: 0
        // refFVisibleErrCount: 8
        // refFVisibleErrRate: 0.0015432098765432098
        // visibleRate: 0.17341820987654322
        // xDrawErrRate: 0.0015432098765432098
        // xDrawPerPointTime: 71.83641975308643
        // xDrawTime: 0.0003724
        // xDrawUnVisibleErrCount: 0
        // xDrawUnVisibleErrRate: 0
        // xDrawVisibleErrCount: 8
        // xDrawVisibleErrRate:

        colHeaders: [
          "",
          "",
          "",

          "R3()",
          "OSD()",
          "FOSD()",
          "()",
          "XDraw()",

          "OSD",
          "OSD",
          "OSD",

          "FOSD",
          "FOSD",
          "FOSD",

          "",
          "",
          "",

          "XDraw",
          "XDraw",
          "XDraw"
        ], //or 
        columns: [
          {
            data: "r",
            type: "numeric"
          },
          {
            data: "allPointCount",
            type: "numeric"
          },
          {
            data: "visibleRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },

          {
            data: "r3Time",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "osdTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "fosdTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "refFTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "xDrawTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },

          {
            data: "osdErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "osdVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "osdUnVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },

          {
            data: "fosdErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "fosdVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "fosdUnVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },

          {
            data: "refFErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "refFVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "refFUnVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },

          {
            data: "xDrawErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "xDrawVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "xDrawUnVisibleErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          }
        ],
        currentRowClassName: "currentRow", //
        currentColClassName: "currentCol", //
        autoWrapRow: true, //
        fillHandle: true, // possible values: true, false, "horizontal", "vertical"
        fixedColumnsLeft: 0, //
        fixedRowsTop: 0, //
        manualColumnFreeze: true, //
        manualColumnMove: true, //
        manualRowMove: true, //
        manualColumnResize: true, //
        manualRowResize: true, //
        comments: true, //
        columnSorting: true, //
        stretchH: "all", //last:none
        licenseKey: "non-commercial-and-evaluation"
      }
    };
  },

  components: {
    HotTable,
    HeaderTittle
  },
  methods: {
    percentageCellRender(instance, td, row, col, prop, value, cellProperties) {
      if (value != null) {
        if (value == 0) td.innerHTML = "0";
        else td.innerHTML = (value * 100).toFixed(4) + "%";
      }
      return td;
    },
    startTest() {
      var _this = this;
      _this.info = "";
      _this.isOnTesting = true;
      _this.axios
        .get(
          "http://localhost:8000/api/dem/analysis_all_fixed_r/" +
            _this.r +
            "/" +
            _this.seeHeight +
            "/" +
            _this.testCount
        )
        .then(
          res => {
            _this.hotSettings.data = res.data;
            _this.isOnTesting = false;
            _this.info =
              "" + res.data.length + "50";
          },
          res => {
            // 
            alert("");
            _this.isOnTesting = false;
          }
        );
    }
  }
};
</script> 

<style scoped>
input {
  width: 60px;
}
.left-content {
  text-align: left;
}
.coverLayout {
  position: absolute;
  left: 0;
  top: 0;
  background: rgb(214, 214, 214);
  width: 100%;
  height: 100%;
  z-index: 100;
  filter: alpha(Opacity=50);
  -moz-opacity: 0.5;
  opacity: 0.8;
}
</style>
