<template>
  <div>
    <HeaderTittle Title=""></HeaderTittle>
    <div style="margin:10px">
      <div class="left-content">
        ()
        <input v-model="r">
        ()
        <input v-model="seeHeight">
        
        <input v-model="maxCount">
        <button @click="startTest" style="margin-left:10px"></button>
      </div>
      <div class="left-content">
        DEM30119~12041~42
        0.027(3010)
      </div>
      <br>
      <HotTable
        :root="root"
        ref="testHot"
        :settings="hotSettings"
        style="overflow:auto; min-height: 400px;"
      ></HotTable>
      <div class="left-content">{{info}}</div>
    </div>
    <div v-bind:class="{coverLayout:isOnTesting}">
      <div style="font-size:50px;margin-top:400px;">...</div>
    </div>
  </div>
</template>

<script>
import { HotTable } from "@handsontable/vue";
import "../../node_modules/handsontable/dist/handsontable.full.css";
import Handsontable from "handsontable";
import { truncate } from "fs";
import HeaderTittle from "./HeaderTittle.vue";

export default {
  data: function() {
    return {
      info: "",
      r: 0.01,
      seeHeight: 2,
      maxCount: 10,
      isOnTesting: false,
      root: "test-hot",
      hotSettings: {
        data: [],
        minRows: 1, //
        minCols: 5,
        maxRows: 10000000, //
        maxCols: 20,
        rowHeaders: true, //html

        colHeaders: [
          "",
          "",
          "",

          "OSD()",
          "FOSD()",
          "()",
          "XDraw()"
        ], //or 
        columns: [
          {
            data: "r",
            type: "numeric"
          },
          {
            data: "allPointCount",
            type: "numeric"
          },
          {
            data: "visibleRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },

          {
            data: "osdTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "fosdTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "refFTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "xDrawTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          }
        ],
        currentRowClassName: "currentRow", //
        currentColClassName: "currentCol", //
        autoWrapRow: true, //
        fillHandle: true, // possible values: true, false, "horizontal", "vertical"
        fixedColumnsLeft: 0, //
        fixedRowsTop: 0, //
        manualColumnFreeze: true, //
        manualColumnMove: true, //
        manualRowMove: true, //
        manualColumnResize: true, //
        manualRowResize: true, //
        comments: true, //
        columnSorting: true, //
        stretchH: "all", //last:none
        licenseKey: "non-commercial-and-evaluation"
      }
    };
  },

  name: "handsonTable",
  components: {
    HotTable,
    HeaderTittle
  },
  methods: {
    percentageCellRender(instance, td, row, col, prop, value, cellProperties) {
      if (value != null) {
        if (value == 0) td.innerHTML = "0";
        else td.innerHTML = (value * 100).toFixed(4) + "%";
      }
      return td;
    },
    startTest() {
      var _this = this;
      _this.info = "";
      _this.isOnTesting = true;
      _this.axios
        .get(
          "http://localhost:8000/api/dem/analysis_time_fixed_r/" +
            _this.r +
            "/" +
            _this.seeHeight +
            "/" +
            _this.maxCount 
        )
        .then(
          res => {
            _this.hotSettings.data = res.data;
            _this.isOnTesting = false;
            _this.info =
              "" + res.data.length + "50";
          },
          res => {
            // 
            alert("");
            _this.isOnTesting = false;
          }
        );
    }
  }
};
</script> 

<style scoped>
input {
  width: 60px;
}
.left-content {
  text-align: left;
}
.coverLayout {
  position: absolute;
  left: 0;
  top: 0;
  background: rgb(214, 214, 214);
  width: 100%;
  height: 100%;
  z-index: 100;
  filter: alpha(Opacity=50);
  -moz-opacity: 0.5;
  opacity: 0.8;
}
</style>
