<template>
  <div>
    <HeaderTittle v-bind:Title="MethodName"></HeaderTittle>
    <div style="margin:10px">
      <div class="left-content">
        ()
        <input v-model="minlon" />
        ()
        <input v-model="minlat" />
        ()
        <input v-model="maxlon" />
        ()
        <input v-model="maxlat" />
        ()
        <input v-model="seeHeight" />
        
        <input v-model="testCount" value="5" />
        <button @click="startTest" style="margin-left:10px"></button>
      </div>
      <div class="left-content">
        DEM30119~12041~42
        0.027(3010)
      </div>
      <br />
      <HotTable
        :root="root"
        ref="testHot"
        :settings="hotSettings"
        style="overflow:auto; min-height: 400px;"
      ></HotTable>
      <div class="left-content">{{info}}</div>
    </div>
    <div v-bind:class="{coverLayout:isOnTesting}">
      <div style="font-size:50px;margin-top:400px;">...</div>
    </div>
  </div>
</template>

<script>
import { HotTable } from "@handsontable/vue";
import "../../node_modules/handsontable/dist/handsontable.full.css";
import Handsontable from "handsontable";
import { truncate } from "fs";
import HeaderTittle from "./HeaderTittle.vue";

export default {
  props: {
    analysisMethod: String,
    MethodName: String,
    HeaderTittle
  },
  data: function() {
    return {
      info: "",
      minlon: 119.15,
      maxlon: 119.95,
      minlat: 41.15,
      maxlat: 41.95,
      seeHeight: 2,
      testCount: 10,
      isOnTesting: false,
      root: "test-hot",
      hotSettings: {
        data: [],
        minRows: 1, //
        minCols: 5,
        maxRows: 10000, //
        maxCols: 20,
        rowHeaders: true, //html
        colHeaders: [
          "",
          "",
          "",
          "R3()",
          "OSD()",
          "OSD()",
          "",
          "",
          "",
          "",
          ""
        ], //or 
        columns: [
          {
            data: "location",
            type: "text"
          },
          {
            data: "allPointCount",
            type: "numeric"
          },
          {
            data: "visibleRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "r3Time",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "time",
            type: "numeric",
            numericFormat: {
              pattern: "0.00000"
            }
          },
          {
            data: "perPointTime",
            type: "numeric",
            numericFormat: {
              pattern: "0.00"
            }
          },
          {
            data: "osdAllErrCount",
            type: "numeric"
          },
          {
            data: "osdErrRate",
            type: "numeric",
            renderer: this.percentageCellRender
          },
          {
            data: "crossCount",
            type: "numeric"
          },
          {
            data: "crossCaculateCount",
            type: "numeric"
          },
          {
            data: "crossFailCount",
            type: "numeric"
          }
        ],
        currentRowClassName: "currentRow", //
        currentColClassName: "currentCol", //
        autoWrapRow: true, //
        fillHandle: true, // possible values: true, false, "horizontal", "vertical"
        fixedColumnsLeft: 0, //
        fixedRowsTop: 0, //
        manualColumnFreeze: true, //
        manualColumnMove: true, //
        manualRowMove: true, //
        manualColumnResize: true, //
        manualRowResize: true, //
        comments: true, //
        columnSorting: true, //
        stretchH: "all", //last:none
        licenseKey: "non-commercial-and-evaluation"
      }
    };
  },

  name: "handsonTable",
  components: {
    HotTable,
    HeaderTittle
  },
  methods: {
    percentageCellRender(instance, td, row, col, prop, value, cellProperties) {
      if (value != null) {
        if (value == 0) td.innerHTML = "0";
        else td.innerHTML = (value * 100).toFixed(4) + "%";
      }
      return td;
    },
    startTest() {
      var _this = this;
      _this.info = "";
      _this.isOnTesting = true;
      _this.axios
        .get(
          "http://localhost:8000/api/dem/" +
            _this.analysisMethod +
            "/" +
            _this.minlon +
            "/" +
            _this.maxlat +
            "/" +
            _this.maxlon +
            "/" +
            _this.minlat +
            "/" +
            _this.seeHeight +
            "/" +
            _this.testCount
        )
        .then(
          res => {
            _this.hotSettings.data = res.data;
            _this.isOnTesting = false;
            _this.info =
              "" + res.data.length + "50";
          },
          res => {
            // 
            alert("");
            _this.isOnTesting = false;
          }
        );
    }
  }
};
</script> 

<style scoped>
input {
  width: 60px;
}
.left-content {
  text-align: left;
}
.coverLayout {
  position: absolute;
  left: 0;
  top: 0;
  background: rgb(214, 214, 214);
  width: 100%;
  height: 100%;
  z-index: 100;
  filter: alpha(Opacity=50);
  -moz-opacity: 0.5;
  opacity: 0.8;
}
</style>
